import Header from "./components/Header";
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import Series from "./components/Series";
import React, { useState } from 'react'

function App() {
  const [serie, setSerie] = useState('spartacus blood')
  return (
    <Router>
      <div className="App">
        <Header setSerie={setSerie}/>
        <Switch>
          <Route path='/' exact >
            <Series serie={serie}/>
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
